import React from 'react';
import '../App'
import All from "../comp/s.mp3";
import wrong from '../comp/wrong.mp3';



export default class Child extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      istrue: false,
      a: ["", "", "", "", "", "", "", "", ""],
      catcharr: [],
      emt: [],
      winner: "",
    };
  }


    audio1 = new Audio(All);
    audio2 = new Audio(wrong)

  winnerDivs=(arr,winner)=>{
      var b = ["", "", "", "", "", "", "", "", ""]
      b.splice(arr[0],1,winner)
      b.splice(arr[1],1,winner)
      b.splice(arr[2],1,winner)
      this.setState({a:b}) 
  }

  playagainHandler=()=>{
      window.location.reload(false)
      this.setState({
        a: ["", "", "", "", "", "", "", "", ""],
      });
  }

  handelClick = (index) => {
    this.audio1.play();
    
    var arr = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 4, 8],
      [2, 4, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 3, 6],
    ];

    if (this.state.emt.includes(index) !== true) {
      this.setState(
        {
          emt: [...this.state.emt, index],

          istrue: !this.state.istrue,
          a: [
            ...this.state.a.slice(0, index),
            this.state.istrue ? "X" : "O",
            ...this.state.a.slice(index + 1),
          ],
        },
        () => {
          for (let i = 0; i < arr.length; i++) {
            if (
              this.state.a[arr[i][0]] !== "" &&
              this.state.a[arr[i][0]] === this.state.a[arr[i][1]] &&
              this.state.a[arr[i][1]] === this.state.a[arr[i][2]]
            ) {
              // console.log(this.state.a[item[0]] + " Is the Winner")
              this.audio2.play();
            //   console.log(arr[i])
              this.winnerDivs(arr[i], this.state.a[arr[i][0]]);
             
              this.setState({
                winner: this.state.a[arr[i][0]] + " is the Winner",
                
                istrue: false,
              });
              break;
            }
          }
        }
      );

      if(this.state.emt.length===8 && this.state.winner === ""){
          this.setState({winner:"Match Draw!!"})
          this.audio2.play();   
      }
    }
  };

  render() {

    return (
      <div>
        <div>
          <h1 className="winner">{this.state.winner}</h1>
        </div>
        <div className="ticTak">
          <div className="row row1">
            <div onClick={() => this.handelClick(0)}>{this.state.a[0]}</div>
            <div onClick={() => this.handelClick(1)}>{this.state.a[1]}</div>
            <div onClick={() => this.handelClick(2)}>{this.state.a[2]}</div>
          </div>

          <div className="row row2">
            <div onClick={() => this.handelClick(3)}>{this.state.a[3]}</div>
            <div onClick={() => this.handelClick(4)}>{this.state.a[4]}</div>
            <div onClick={() => this.handelClick(5)}>{this.state.a[5]}</div>
          </div>

          <div className="row row3">
            <div onClick={() => this.handelClick(6)}>{this.state.a[6]}</div>
            <div onClick={() => this.handelClick(7)}>{this.state.a[7]}</div>
            <div onClick={() => this.handelClick(8)}>{this.state.a[8]}</div>
          </div>
        </div>

        <div id="strikethrough"><div></div></div>
        <div className="btn">
          {this.state.winner !== "" ? (
            <button onClick={this.playagainHandler}>Play Again!</button>
          ) : (
            ""
          )}
        </div>
      </div>
    );
  }
}